
    import { defineComponent } from "vue";

    export default defineComponent({
        name: 'BadgeComponent',
        props: {
            text: {
                required: true,
                type: String
            },
            paddingX: {
                required: false,
                type: String
            },
            paddingY: {
                required: false,
                type: String
            },
            color: {
                required: false,
                type: String
            },
            level: {
                required: false,
                type: String
            },
            done: {
                required: true,
                type: Boolean
            }
        }
    });
