
    import Product from "@/types/Product";
    import productAPI from "@/api/product";
    import { defineComponent, onMounted, ref } from "vue";
    import { useUserState } from "@/stores/user-state";
    import { storeToRefs } from "pinia";
    import BadgeComponent from "./BadgeComponent.vue";
    import { XIcon } from "@heroicons/vue/outline";
    import type IUser from "@/types/User";
    import userAPI from "@/api/user";

    export default defineComponent({
        name: 'RenderOrder',
        props: {
            ord: {
                required: true,
                type: Object
            },
            worker: {
                required: false,
                type: Boolean
            }
        },
        setup(props) {
            const product = ref<Product>();

            const userState = useUserState();
            const { user } = storeToRefs(userState);

            const userOrdered = ref<IUser>();

            onMounted(() => {
                productAPI.getSingleProduct(props.ord.product_id)
                          .then(res => {
                              product.value = res.data;
                          })
                          .catch(err => console.log(err));

                userAPI.getUserById(props.ord.user_id)
                       .then(res => {
                           userOrdered.value = res.data;
                       })
                       .catch(err => console.log(err));
            });

            return {
                product,
                user,
                userOrdered
            }
        },
        components: {
            BadgeComponent,
            XIcon
        },
        methods: {
            removeItem() {
                const ok = window.confirm('Are You Sure ?');
                if (!ok) return;
                this.$emit('remove-item');
            },

            markAsDone() {
                this.$emit('mark-as-done');
            }
        }
    });
